<template>
  <span>
    <span
        v-if="datePanelLabel"
        v-show="datePanelLabel.labels[0].type === 'year' || currentView === 'date'"
        :class="[datePrefixCls + '-header-label']"
        @click="datePanelLabel.labels[0].handler">{{ datePanelLabel.labels[0].label }}
    </span>
  </span>
</template>

<script>
export default {
    props: {
        datePanelLabel: Object,
        currentView: String,
        datePrefixCls: String
    }
};
</script>



